import React, { useState, useEffect} from 'react';
import './Components.css';
import profilePic from '../images/updatedpic.jpg'


function qualifications(){
	return(
			<ul>
						<li>BCs with Honours in Ethical Hacking at Abertay University</li>
						<li>I am a systems security Certified practitioner (SSCP)</li>
						<li>I have sustained membership with (ISC)² since 2020. Continued certification is based on earning Continuing Professional Education (CPE) points through security related work/training. A minimum amount of points is required each year.</li>
						<li>In progress: Certified Information Systems Security Professional (CISSP)</li>
						<li>I have several certificates for completion of minor online courses in development and security.</li>
			</ul>
		);
};
function secExp(){
	return(
			<ul>
						<li>Studying ethical hacking for several years at university has given me a solid foundation on the technical aspects of Information Security.</li>
						<li>Throughout my time at university, as well as my time as an analyst I have completed several security related projects. Please look at the project page for more details. Please note that this isn't an exhaustive list of all projects I have participated in, some were too sensitive to disclose, and others felt too minor too include.</li>
						<li>I still do penetration testing as a hobby through completion of online labs from "hack the box".</li>
						<li>After graduating university, I started work as a CyberSecurity Analyst at a financial technology company and I have been working there since July 2019. This was a very dynamic role and my duties and responsibilities were always changing over the 3 years I worked as a security analyst. Some of these duties and responsibilities include:
							<ul>
								<li>Discussing and evidencing security controls to Clients / 3rd party Auditors</li>
								<li>Supplier due diligence</li>
								<li>Security Operations: Occasionally reviewing logs, employee requested software, managing security incidents, phishing attempts, etc.</li >
								<li>Ensuring that Security requirements are met on our client software releases</li >
								<li>Arranging 3rd party penetration tests</li >
								<li>Reviewing Application security vulnerabilities.</li >
								<li>Assisting user access reviews of corporate and client systems</li >
								<li>Physical security reviews of our offices and data - centres</li >
								<li>Vulnerability management</li >
								<li>Ensuring compliance to external standards, such as PCI DSS.</li >
								<li>Gathering and compiling security management information for clients</li >
								<li>Providing user guidance on security best practice, and establishing security culture and awareness</li >
								<li>Reviewing standards and policies.</li >
							</ul>
						</li>
						<li>Near the beginning of 2022 I started devloping my skills as an Application Security Engineer. As an Application Security Engineer, it is ultimately my job to ensure that security requirements are being met throughout the SDLC. This has become increasingly challenging as software development has become more rapid due to agile development becoming more commonplace. Security has no choice but to speed up with the rest of development if we don’t want to be seen as a bottleneck. This can be done through shifting left and focussing on these 3 key areas: education, secure design principles, and automated security testing. These were some of my duties and responsibilities:
							<ul>
								<li>Configuring SAST tools, reviewing rule profiles, reviewing vulnerabilities, providing guidance to developers around this.</li >
								<li>Automated Pen testing/DAST: This tool provided dynamic security testing and manual analysis/verification as a service. I would review the final output and assess the severity of any vulnerabilities</li >
								<li>Management of 3rd party penetration tests: I would manage tests against our client platforms and review the output.</li >
								<li>SCA: Our client platforms would be scanned by our Security Composition Analysis tool, which would identify vulnerabilities in any 3rd party libraries that we use.I would assess the output.</li >
								<li>Manual verification of findings using burpsuite.</li >
								<li>Management of Security Champions program.I was responsible for engaging with these specialised developers and assigning tasks.</li >
								<li>Management of Developer security training.I would select training modules that would be assigned to our developers.</li >
								<li>Reviewing our security defect backlog for trends and weaknesses within the SDLC.This information would then be used to determine if any architectural or security testing changes are needed to be made.</li >
								<li>Ensuring that Application Security requirements are met on our client software releases.</li >
								<li>Reporting application security management information to clients, and internally.</li >
							</ul>
						</li>
						</ul>
		);
};
function devExp(){
	return(
			<ul>
						<li>During my time at university, I had sat a module focussed on web application Development, and I also had several modules centred around the testing of web apps, and programming.</li>
						<li>After graduating university and beginning my career in Information Security, I decided I wanted to continue pursuing web application development as a hobby. To do this I decided to sit the online course "The Complete Web Developer in 2020: Zero to Mastery" by Andrei Neagoi. This course covered both front end, and back end development. It helped me to master the fundamental concepts and focussed on teaching modern technologies being used in 2021.</li>
						<li>Since sitting this course, I have also sat other courses on Udemy, pluralsight, and youtube. This has further developed my development skills in other areas, with a special focus on Microsoft .NET technologies.</li>
						<li>Please look at the 'Projects' page to see some of the web applications and other developer related projects I have worked on.</li>
			</ul>
		);
};


const ExperienceWindow = () => {
	

	

	const [textContent, setTextContent] = useState(qualifications())

	// The following block of code (40 lines) is for changing the button css properties when selected
	const [qualificationButton, setQualificationButton] = useState("buttonTest  w-third h-100")
	const [developerButton, setDeveloperButton] = useState("buttonTest  w-third h-100")
	const [securityButton, setSecurityButton] = useState("buttonTest  w-third h-100")
	const [buttonSelected, SetButtonSelected] = useState("qualifications")
	useEffect(() => {
	   	if(buttonSelected === "security"){
	      	setSecurityButton("buttonTestSelected  w-third h-100 b--dark-red");
	      	setDeveloperButton("buttonTest  w-third h-100");
			setQualificationButton("buttonTest  w-third h-100");
	    }
	    else if (buttonSelected === "developer"){
	    	setDeveloperButton("buttonTestSelected  w-third h-100 b--dark-red");
	    	setSecurityButton("buttonTest  w-third h-100");
	    	setQualificationButton("buttonTest  w-third h-100");
	    }
	    else{
	    	setQualificationButton("buttonTestSelected  w-third h-100");
	    	setSecurityButton("buttonTest  w-third h-100");
	    	setDeveloperButton("buttonTest  w-third h-100");
	    };
	// eslint-disable-next-line
	}, [textContent]);

	function secbutt(){
		SetButtonSelected("security")
		setTextContent(secExp())
	}
	function devbutt(){
		SetButtonSelected("developer")
		setTextContent(devExp())
	}
	function quabutt(){
		SetButtonSelected("qualifications")
		setTextContent(qualifications())
	}

	return(
		<div  className = "windowDesignExperience flex">
			<div className = "outline mobileExpPic"><img className="h-100 w-100 v-top" alt="Logo" src={profilePic} /></div>
			<div className = "outline flex-column mobileExpText">
				<div className = "outline w-100 flex justify-center expColumn1">
					<button className={qualificationButton} onClick={() => quabutt()}>Qualifications</button>
					<button className = {securityButton} onClick={() => secbutt()}>Security Experience</button>
					<button className = {developerButton} onClick={() => devbutt()}>Developer Experience</button>
				</div>
				<div className = "outline w-100 flex justify-center expColumn2">
						{textContent}
				</div>
			</div>
		</div>
	);
};

export default ExperienceWindow;